import {
    call, //,   -   ()  ,  ,     .
    put, //  dispatch  actionCreator  .  .
    takeLeading, //        
    delay, //   
    takeEvery, //   -     
    select, //  useSelector  
    takeLatest, //        
    take, //      ,      Action   take(pattern).  .
    fork, //   put -   
    all, //  -           
    race,//  -         
} from "@redux-saga/core/effects";
import {apiJsonPlaceholder} from "../../api/api";
import {AppActions} from "../reducers/app-reducer";
import {AllPostsActions, GET_ALL_POSTS, GET_COMMENTS_BY_POST_ID} from "../reducers/all-posts-reducer";
import {CommentType, ErrorType, JSPHResponseType, PostType, UserDataType} from "../../common/commonTypes/commonTypes";
import {GET_USER_DATA, UserActions} from "../reducers/user-reducer";
import {GlobalStateType} from "../store/store-redux";

const {toggleIsFetchingAC, setInitialisedAppAC, setErrorAC} = AppActions //    
const {setAllPostsAC, setCommentsByPostIdAC, setShowCommentsAC} = AllPostsActions //       
const {setUserDataAC} = UserActions //     ID

function* workerInitialApp() { //      
    yield put( toggleIsFetchingAC( true ) ) //  
    yield delay( 500 ) //   

    const {response, error}:{response:JSPHResponseType<Array<PostType>>, error: ErrorType}  =
        yield call( apiJsonPlaceholder.getPosts ) //     

    if (response) { //   
        yield put( setAllPostsAC( response.data ) )//     
        yield put( setInitialisedAppAC() ) //   -  
    } else {
        yield put( setErrorAC( error) )//     
    }
    yield put( toggleIsFetchingAC( false ) ) //  
}

function* workerGetUserDataById(props: {type: string, id: number}) { //    
    yield put( toggleIsFetchingAC( true ) ) //  
    yield delay( 500 ) //   
    const {response, error}:{response:JSPHResponseType<Array<UserDataType>>, error: ErrorType}  =
        yield call( apiJsonPlaceholder.getUserDataById, props.id )
    if (response) {//   
        yield put( setUserDataAC( response.data[0] ) )//     
    } else {
        yield put( setErrorAC( error) )//     
    }
    yield put( toggleIsFetchingAC( false ) ) //  
}

function* workerGetCommentsById(props: {type: string, postId: number}) { //    
    //   ShowComments -   ,  
    const ShowComments:Array<number> = yield select((state:GlobalStateType) => state.allPosts.ShowComments) //     

    if (ShowComments.includes(props.postId)) {//   ID   ShowComments, 
        //    postId  ShowComments   AC
        yield put( setShowCommentsAC(ShowComments.filter((postId:number)=>postId!==props.postId) ) )//  id ,    

    }
    else {//  Id   ShowComments, 
        //1)    (  )
        yield put( toggleIsFetchingAC( true ) ) //  
        yield delay( 500 ) //           const response: Array<CommentType> = yield call( apiJsonPlaceholder.getCommentsByPostId, props.postId )
           const {response, error}:{response:JSPHResponseType<Array<CommentType>>, error: ErrorType}  =
               yield call( apiJsonPlaceholder.getCommentsByPostId, props.postId )
        if (response) {//   
            yield put( setCommentsByPostIdAC( response.data ) )//     
            //2)  postId  ShowComments   AC
            yield put( setShowCommentsAC([...ShowComments, props.postId] ) )//     
        } else {
            yield put( setErrorAC( error) )//     
        }
        yield put( toggleIsFetchingAC( false ) ) //  

    }
}

function* watchSaga() {
    yield takeLeading( GET_ALL_POSTS, workerInitialApp );
    yield takeEvery( GET_COMMENTS_BY_POST_ID, workerGetCommentsById )
    yield takeEvery( GET_USER_DATA, workerGetUserDataById )
}

export default function* rootSaga() {
    yield watchSaga()
}
