import {InferActionsTypes} from "../store/store-redux";
import {ErrorType} from "../../common/commonTypes/commonTypes";

export const SET_INITIALISED_APP = "myApp/app-reducer/SET_INITIALISED_APP"; //  
export const TOGGLE_IS_FETCHING = "myApp/users-reducer/TOGGLE_IS_FETCHING";//  / 
export const SET_ERROR = "myApp/users-reducer/SET_ERROR"; //    

export const AppActions = {
    setInitialisedAppAC: () => { //     
        return {type: SET_INITIALISED_APP} as const
    },
    toggleIsFetchingAC: (isFetching: boolean) => {
        return {type: TOGGLE_IS_FETCHING, isFetching} as const
    },
    setErrorAC: (error: ErrorType) => {
        return {type: SET_ERROR, error} as const
    },
}

type AppActionTypes = InferActionsTypes<typeof AppActions>

const initialState = {//     
    initialisedApp: false, //   ?
    isFetching: false, //   ()
    error: {} as ErrorType
}

export type AppInitialStateType = typeof initialState

const appReducer = (state: AppInitialStateType = initialState, action: AppActionTypes): AppInitialStateType => {//  
    let stateCopy: AppInitialStateType;//      
    switch (action.type) {
        case SET_INITIALISED_APP:  //   
            stateCopy = {
                ...state, //   
                initialisedApp: true, //      true
            }
            return stateCopy; //     
        case TOGGLE_IS_FETCHING:
            stateCopy = {
                ...state,
                isFetching: action.isFetching
            }
            return stateCopy; //     
        case SET_ERROR:
            stateCopy = {
                ...state,
                error: action.error
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}

export default appReducer