import {AllPostsInitialStateType, PaginationDataType} from "../../../redux/reducers/all-posts-reducer";
import React, {useEffect, useState} from "react";
import {InputGroup} from "react-bootstrap";
import Form from 'react-bootstrap/Form';
import RenderSortButtonContainer from "../RenderSortButton/RenderSortButtonContainer";
import {useNavigate} from "react-router-dom";
import Image from "react-bootstrap/Image";
import goBack from "../../../assets/svg/back-arrow1.svg";
import s from "../../../common/classes/common.module.css";
import closeCircle from "../../../assets/svg/close-circle.svg"
import Navbar from "react-bootstrap/Navbar";
import Offcanvas from "react-bootstrap/Offcanvas";
import Nav from "react-bootstrap/Nav";
import myAvatar from "../../../assets/jpeg/myAvarat.jpg";
import Container from "react-bootstrap/Container";

type PostsInputRenderType = {
    SearchPostQuery: string,
    PostsInitialState: AllPostsInitialStateType,
    PaginationData: PaginationDataType,
    setSearchPostQuery: (SearchPostQuery: string) => void
    setPaginationData: (PaginationData: PaginationDataType) => void
}
const PostsInputRender: React.FC<PostsInputRenderType> = (
    {SearchPostQuery, setSearchPostQuery, setPaginationData, PostsInitialState, PaginationData}) => {
    console.log( "PostsInputRender" )

    const navigate = useNavigate(); //      ( )

    const goBackRender = <Image src={goBack} className={s.goBack}
                                alt={"go back"} title={"go back"}
                                onClick={() => navigate( -1 )} //      
    />
    const closeButton = <Image className={s.closeButton}
                               src={closeCircle}
                               alt={" "} title={" "}
                               onClick={() => onClearField()} //    
    />
    const [QueryTmp, setQueryTmp] = useState( SearchPostQuery )

    const onChangeSearchPostQuery = (SearchPostQuery: string) => {//    
        setSearchPostQuery( SearchPostQuery ) //   
        if (PaginationData.CurrentPage !== 1) {//   !==1
            console.log( "       1  " )
            setPaginationData( PostsInitialState.PaginationData )
        }
    }

    const onClearField = () => {
        setSearchPostQuery( "" )
        setQueryTmp( "" )
    }

    useEffect( () => { //   input (     )
        const setSearchPostQueryTmp = QueryTmp //    
        const id = setTimeout( () => {
            if (setSearchPostQueryTmp === QueryTmp) { //        
                onChangeSearchPostQuery( QueryTmp ) //     
            }
        }, 500 ); // , 
        return () => {
            clearTimeout( id ); //
        };
    }, [QueryTmp] );


    return <div  className="position-relative">
        {closeButton} {/*    -  */}

        <InputGroup>
            <InputGroup.Text>
                {goBackRender} {/*  */}
            </InputGroup.Text>
            <InputGroup.Text>
                <RenderSortButtonContainer/>
            </InputGroup.Text>
            <Form.Control
                className={s.InputField}
                placeholder="  ..."
                autoFocus={true}
                type="text"
                value={QueryTmp}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                    setQueryTmp( e.target.value )
                }}
            />

            <InputGroup.Text>
                <Navbar.Toggle/>
                <Navbar.Offcanvas  placement="start" >
                    <Offcanvas.Header closeButton>
                        <Offcanvas.Title/>
                    </Offcanvas.Header>
                    <Offcanvas.Body>
                        <Nav>
                            <Nav.Link href="#/"> </Nav.Link>
                            <Nav.Link href="#/about-me"> 
                                <Image fluid={true} src={myAvatar} className={"rounded"}
                                       alt={""} title={``}
                                /></Nav.Link>
                            <Nav.Item> </Nav.Item>
                            <Nav.Link href="mailto:uchenick55@yandex.ru">uchenick55@yandex.ru</Nav.Link>
                        </Nav>
                    </Offcanvas.Body>
                </Navbar.Offcanvas>            </InputGroup.Text>

        </InputGroup>
    </div>

}

export default PostsInputRender