import {InferActionsTypes} from "../store/store-redux";
import {UserDataType} from "../../common/commonTypes/commonTypes";

export const GET_USER_DATA = "myApp/app-reducer/GET_USER_DATA"; //    
export const SET_USER_DATA_TO_STATE = "myApp/app-reducer/SET_USER_DATA_TO_STATE"; //      

export const UserActions = {
    getUserDataAC: (id:number) => { //      ID 
        return {type: GET_USER_DATA, id} as const
    },
    setUserDataAC: (UserData: UserDataType) => { //       
        return {type: SET_USER_DATA_TO_STATE, UserData} as const
    },
}

export type UserActionsTypes = InferActionsTypes<typeof UserActions>

const initialState = {//  
    UserData: {} as UserDataType, //    
}
export type UserInitialStateType = typeof initialState

const UserReducer = (state: UserInitialStateType = initialState, action: UserActionsTypes): UserInitialStateType => {//  
    let stateCopy: UserInitialStateType;//      
    switch (action.type) {
        case SET_USER_DATA_TO_STATE:  //      
            stateCopy = {
                ...state, //   
                UserData: action.UserData, //     
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}

export default UserReducer