import React, {Suspense} from "react";
import {Route, Routes} from "react-router-dom";
import classes from "./ContentContainer.module.css"
import ErrorBoundary from "../../common/ErrorBoundary/ErrorBoundary";
import {Container} from "react-bootstrap";
import s from "../../common/classes/common.module.css"

const AllPostsList2 = React.lazy( () => import("../AllPostsList/AllPostsList") )
const UserPosts = React.lazy( () => import("../UserPosts/UserPosts") )
const AboutMe = React.lazy( () => import("../AboutMe/AboutMe") )

let ContentContainer: React.FC = () => { //      

    return <div>
        <Container className={s.ContentContainer}>
            <ErrorBoundary> {/*   ContentContainer*/}
                <Suspense fallback={
                    <div>...</div>}> {/* ,    Lazy import   fallback   */}
                    <div className={classes.contentContainer}>
                        <Routes> {/*   URL   */}
                            <Route path='' element={<AllPostsList2/>}/> {/*  */}
                            <Route path='/user-posts/*' element={<UserPosts/>}/> {/*  */}
                            <Route path='/about-me/*' element={<AboutMe/>}/> {/* */}
                        </Routes>
                    </div>
                </Suspense>
            </ErrorBoundary>
        </Container>
    </div>
}
export default ContentContainer
