import React from "react";
import RenderSortButton from "./RenderSortButton";
import {AllPostsActions} from "../../../redux/reducers/all-posts-reducer";
import {useDispatch, useSelector} from "react-redux";
import {GlobalStateType} from "../../../redux/store/store-redux";
const { setSortHeaderDirectionAC} = AllPostsActions //       

const RenderSortButtonContainer:React.FC = () => {

    const dispatch = useDispatch()

    //    
    const SortHeaderDirection: boolean | undefined = useSelector( (state: GlobalStateType) => state.allPosts.SortHeaderDirection ) //  

    //     
    const setSortHeaderDirection = (SortHeaderDirection: boolean | undefined) => { //     
        dispatch( setSortHeaderDirectionAC( SortHeaderDirection ) )
    }

    return <div>
        <RenderSortButton //   
            SortHeaderDirection={SortHeaderDirection} setSortHeaderDirection={setSortHeaderDirection}/>
    </div>
}
export default RenderSortButtonContainer